<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form action="#" v-if="!submitted">
      <label for="blog-title">博客标题</label>
      <input type="text" id="blog-title" v-model.lazy="blog.title" required />
      <label for="blog-content">博客内容</label>
      <textarea id="blog-content" v-model.lazy="blog.content"></textarea>
      <div id="checkboxes">
        <input type="checkbox" id="vue" value="Vue.js" v-model="blog.categories">
        <label for="vue">Vue.js</label>
        <input type="checkbox" id="node" value="Node.js" v-model="blog.categories">
        <label for="node">Node.js</label>
        <input type="checkbox" id="react" value="React.js" v-model="blog.categories">
        <label for="react">React.js</label>
        <input type="checkbox" id="angular" value="Angular4" v-model="blog.categories">
        <label for="angular">Angular4</label>        
      </div>
      <label for="author">作者：</label>
      <select id="author" v-model="blog.author">
        <option disabled value="">请选择</option>
        <option v-for="author in authors" :key="author.key">{{ author }}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="submitted">
      <h3>您的博客发布成功～</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题： {{ blog.title }}</p>
      <p>博客内容：</p>
      <p>{{ blog.content }}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="category in blog.categories" :key="category.id">{{ category }}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddBlog',
  data () {
    return {
      blog: {
        title: '',
        content: '',
        categories: [],
        author: ''
      },
      authors: ['Thomas', 'John', 'Lucas'],
      submitted: false // 控制 form 表单以及 发布成功信息的显示与隐藏
    }
  },
  methods: {
    post () {
      this.$http.post('https://wd6295097891walbow.wilddogio.com/posts.json', this.blog)
        .then(data => {
          console.log(data);
          this.submitted = true;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 800px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"], textarea, select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
  margin-right: 10px;
}

#checkboxes input {
  display: inline-block;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

#preview h3 {
  margin-top: 10px;
}
</style>



