<template>
  <div id="show-blog" v-theme:background>
    <h2>博客总览</h2>
    <input type="search" v-model="search" placeholder="Search...">
    <div v-for="blog in filterBlogs" :key="blog.id" class="single-blog">
      <router-link :to="'/blog/' + blog.id">
        <h3 v-rainbow>{{ blog.title | to-uppercase }}</h3>
      </router-link>
      <article>
        {{ blog.content | snippet }}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShowBlog',
  data () {
    return {
      blogs: [],
      search: ''
    }
  },
  methods: {
  },
  created () {
    this.$http.get('https://wd6295097891walbow.wilddogio.com/posts.json')
    .then(
      data => {
        return data.json()
    })
    .then(
      data => {
        for (let key in data) {
          data[key].id = key;
          this.blogs.push(data[key]);
        }
      }
    );
  },
  computed: {
    filterBlogs () {
      return this.blogs.filter(
        blog => blog.title.toLowerCase().match(this.search.toLowerCase())
      );
    }
  },

  // 自定义局部过滤器
  filters: {
    toUppercase (value) {
      return value.toUpperCase();
    }
  },

  // 自定义局部指令
  directives: {
    'rainbow': {
      bind (el, binding, vnode) {
        el.style.color = '#' + Math.random().toString(16).slice(2, 8);
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blog {
  margin: 20px auto;
  max-width: 800px;
  padding: 20px;
}

input[type="search"] {
  border-radius: 5px;
  border: 1px solid #ebebeb; /* 对默认的border:2px inset覆盖，否则下面的样式不起作用 */
  padding: 10px;
  color: #333;
  width: 30%;
  height: 30px;
  outline: none;
}

.single-blog {
  margin: 20px 0;
  padding: 20px;
  box-sizing: border-box;
  background: #eee;
  border-radius: 6px;
}

a {
  text-decoration: none;
}
</style>



