<template>
  <div id="single-blog">
    <h1>{{ blog.title }}</h1>
    <article>{{ blog.content }}</article>
    <p>作者：{{ blog.author }}</p>
    <ul>
      <li v-for="category in blog.categories" :key="category.id">
        {{ category }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SingleBlog',
  components: {
  },
  data () {
    return {
      id: this.$route.params.id,
      blog: {}
    }
  },
  created () {
    this.$http.get('https://wd6295097891walbow.wilddogio.com/posts/' + this.id + '.json')
      .then(
        data => {
          return data.json();
        }
      )
      .then(
        data => {
          this.blog = data;
        }
      );
  }
}
</script>

<style scoped>
#single-blog {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>



